/*
- Typography
*/

/* Google font import */
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap");

body {
  font-family: "Inter", sans-serif;
  font-size: 16px;
  color: #838ea4;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #0a2463;
}

h1 {
  font-size: 3.5rem;
  line-height: 1.4;
}

h2 {
  font-size: 2.75rem;
  line-height: 1.25;
}

h3 {
  font-size: 2.25rem;
}

h4 {
  font-size: 1.65rem;
}

p {
  font-size: 1.2rem;
  margin-bottom: 1.25rem;
}
.small {
  font-size: 0.95rem;
}
